import React from 'react';
import { Typography, Grid } from '@material-ui/core';
import { TagCloud } from "react-tagcloud";

const frontend = [
  { value: "JavaScript", count: 40 }, { value: "React", count: 40 },
  { value: "Vue.js", count: 25 },
  { value: "HTML5", count: 33 }, { value: "CSS3", count: 20 },
  { value: "Webpack", count: 22 }, { value: "Gulp", count: 10 },
  { value: "ECMAScript 6", count: 25 }, { value: "Jest", count: 15 },
  { value: "Mocha", count: 17 }, { value: "Angular.js", count: 40 },
  { value: "NPM", count: 11 }
];

const backend = [
  { value: "Git", count: 40 }, { value: "Next.js", count: 25 },
  { value: "SQL", count: 30 }, { value: "ASP.net Core", count: 40 },
  { value: "Entity Framework", count: 25 }, { value: "Express.js", count: 25 },
  { value: "Node.js", count: 30 }, { value: "ASP.net MVC", count: 20 }, 
];

const optionsFrontend = {
  luminosity: 'light',
  hue: 'red'
};

const optionsBackend = {
  luminosity: 'light',
  hue: 'blue'
};

let seed = 1337;
function random() {
  const x = Math.sin(seed++) * 10000;
  return x - Math.floor(x);
}

const Skills = () => {
  return <div>
    <Typography variant="h4" gutterBottom className="title">
      Skills Cloud
    </Typography>

    <Grid container spacing={24}>
      <Grid item xs={12} md={6}>
        <Typography variant="h5" gutterBottom>
          Front-end
        </Typography>
        <TagCloud minSize={15}
          maxSize={40}
          tags={frontend}
          colorOptions={optionsFrontend}
          className="simple-cloud"
          randomNumberGenerator={random}
          key="frontend" />
      </Grid>
      <Grid item xs={12} md={6}>
        <Typography variant="h5" gutterBottom>
          Back-end
        </Typography>
        <TagCloud minSize={15}
          maxSize={40}
          tags={backend}
          colorOptions={optionsBackend}
          className="simple-cloud"
          randomNumberGenerator={random}
          key="backend" />
      </Grid>
    </Grid>
  </div>
};

export default Skills;